.toggle-mnu{
	display: none;
	position: fixed;
	right: rem(24);
	top: rem(10);
  // display: block;
  width: rem(24);
  height: rem(24);
  // margin-top: 14px;
  padding: 0;
  background-color: transparent;
  border: none;

  @include max-screen($sm){
  	display: block;
  }
  span{

		// top: -5px;
  }
  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 7px;
  } 

  span:after{

    top: 14px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
} 
