/*--------------------adjustment----------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

html, body {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

body {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-weight: normal;
  font-size: 1.125rem;
  min-width: 320px;
  position: relative;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: #545971;
}
.lock body {
  overflow: hidden;
  touch-action: none;
  overflow-behavior: none;
}

html, body {
  height: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}
@supports (overflow: clip) {
  .main {
    overflow: clip;
  }
}
.main > * {
  min-width: 0;
}
.main .header {
  flex: 0 0 auto;
}
.main .main-content {
  flex: 1 1 auto;
}
.main .footer {
  flex: 0 0 auto;
}

a {
  color: inherit;
}

img {
  vertical-align: top;
}

ul, li {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
}

p {
  margin: 0;
}

input input[type=text],
input[type=email],
input[type=tel],
textarea {
  appearance: none;
}

.input,
input {
  outline: none;
  display: block;
  width: 100%;
}

textarea.input {
  resize: none;
  padding: 0 0;
}

h1 {
  font-size: 4rem;
}

[class*=__container] {
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  max-width: 91.875rem;
}

.some-selector {
  font-size: 1.25rem;
  padding: 0.875rem 2rem;
}

.hidden {
  display: none;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 52.25%;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

._prod-img {
  position: relative;
  padding-bottom: 80%;
}
._prod-img img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

._fit {
  padding-bottom: 80%;
  position: relative;
}

._fit ._fit-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
}

@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)) {
  .bd-filter-fallback {
    background-color: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(31px);
  }
}

.toggle-mnu {
  display: none;
  position: fixed;
  right: 1.5rem;
  top: 0.625rem;
  width: 1.5rem;
  height: 1.5rem;
  padding: 0;
  background-color: transparent;
  border: none;
}
@media only screen and (max-width: 47.99875em) {
  .toggle-mnu {
    display: block;
  }
}
.toggle-mnu span:after, .toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
}
.toggle-mnu span:after {
  top: 14px;
}
.toggle-mnu span {
  position: relative;
  display: block;
}
.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}
.toggle-mnu.on span {
  background-color: transparent;
}
.toggle-mnu.on span:before {
  transform: rotate(45deg) translate(-1px, 0px);
}
.toggle-mnu.on span:after {
  transform: rotate(-45deg) translate(6px, -7px);
}

.cover {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 31;
  background-color: rgba(20, 34, 87, 0.6);
}
.menu-opened .cover {
  display: block;
}

/*--------------------END adjustment----------------------*/
/*--------------------ui----------------------*/
.block-header {
  display: grid;
  gap: 1.5rem;
}

.block-header--center {
  text-align: center;
}

.block-header__title {
  color: #171921;
  font-weight: 700;
  font-size: 3.25rem;
  line-height: 1.23;
}
@media (max-width: 90em) {
  .block-header__title {
    font-size: clamp( 1.5rem , 1.15rem  +  1.75vw , 3.25rem );
  }
}

.block-header__title--big {
  font-size: 4rem;
}
@media (max-width: 90em) {
  .block-header__title--big {
    font-size: clamp( 2.75rem , 2.5rem  +  1.25vw , 4rem );
  }
}

.block-header--horizontal {
  grid-template-columns: minmax(auto, 36.625rem) minmax(auto, 38.3125rem);
  justify-content: space-between;
  align-items: end;
}

.block-header__title--wht {
  color: #fff;
}

.block-header__text p {
  line-height: 155.556%;
}

/*--------------------END ui----------------------*/
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 30;
  background-color: #142257;
}

.header__container {
  max-width: 99.625rem;
  min-height: 5rem;
  display: grid;
  grid-template-columns: 11.25rem 1fr 11.25rem;
  align-items: center;
  gap: 0.9375rem;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
}
@media only screen and (max-width: 47.99875em) {
  .header__container {
    min-height: 3.5rem;
  }
}

.header__logo img {
  max-width: 100%;
}

.header__menu {
  justify-self: center;
}

@media screen and (max-width: 47.99875em) {
  .menu-header__body {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transition: all 0.4s;
    transform: translateX(100%);
    overflow-y: auto;
  }
}
.menu-opened .menu-header__body {
  transform: translateX(0);
}
@media only screen and (max-width: 47.99875em) {
  .menu-header__body {
    display: flex;
    flex-direction: column;
    padding-bottom: 2.25rem;
    max-width: 17rem;
    background-color: #142257;
    overflow-y: visible;
    z-index: 32;
  }
}

.menu-header__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 0.3125rem;
  column-gap: 3.5625rem;
}
@media (max-width: 90em) {
  .menu-header__list {
    column-gap: clamp( -0.3125rem , -1.0875rem  +  3.875vw , 3.5625rem );
  }
}
@media only screen and (max-width: 47.99875em) {
  .menu-header__list {
    padding: 0 1.5rem;
    flex-direction: column;
    flex: 1 1 auto;
    text-align: center;
    overflow-y: auto;
    flex-wrap: nowrap;
  }
}

.menu-header__item {
  list-style-type: none;
}

.menu-header__link {
  font-style: normal;
  font-weight: 700;
  line-height: 133.333%;
  color: #fff;
  text-decoration: none;
}
@media only screen and (max-width: 47.99875em) {
  .menu-header__link {
    display: block;
    padding: 2rem;
  }
}
@media (any-hover: hover) {
  .menu-header__link:hover {
    text-decoration: underline;
  }
}

.menu-header__top {
  flex-shrink: 0;
  display: none;
  align-items: center;
  justify-content: space-between;
  height: 3.5rem;
  border-bottom: 1px solid #1F3584;
}
@media only screen and (max-width: 47.99875em) {
  .menu-header__top {
    display: flex;
    padding: 0 1.5rem;
  }
}

.menu-header__logo {
  flex: 0 0 11rem;
}

.toggle-mnu--close {
  position: static;
  transform: scale(0.8);
  margin-top: 0.4375rem;
}
.toggle-mnu--close.on span:after {
  transform: rotate(-45deg) translate(5px, -6px);
}

.header__schedule {
  justify-self: end;
  display: flex;
  align-items: center;
  column-gap: 0.625rem;
  font-style: normal;
  font-weight: 700;
  line-height: 133.333%;
  color: #fff;
}
.header__schedule:before {
  content: "";
  display: block;
  width: 0.75rem;
  flex: 0 0 0.75rem;
  height: 0.75rem;
  border-radius: 100%;
  background-color: #FF5E5E;
}
@media only screen and (max-width: 47.99875em) {
  .header__schedule {
    justify-content: center;
  }
}

.mob-menu-box {
  z-index: 31;
}
@media screen and (max-width: 47.99875em) {
  .mob-menu-box {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transition: all 0.4s;
    transform: translateX(100%);
    overflow-y: auto;
  }
}
.menu-opened .mob-menu-box {
  transform: translateX(0);
}

/*--------------------main-block----------------------*/
